























import Vue from "vue";

export default Vue.extend({
  name: "RadioInput",
  inheritAttrs: false,
  props: {
    id: String,
    name: String,
    label: String,
    options: Array,
    subLabel: String,
    description: String,
    errorText: String
  },
  methods: {
    hasError(): boolean | null {
      return this.errorText ? false : null;
    }
  }
});
